import React, {Component} from 'react';

class Footer extends Component {

    render() {
        return(
            <div data-aos-anchor="top-top" data-aos="fade" data-aos-duration="900" data-aos-delay="2200"   >
            
            <br/>
            <footer  className="page-footer indigo darken-4 z-depth-3">
              <div style={{paddingBottom: '4px'}} className="container center">
          
                <a data-position="top" data-tooltip="Facebook" target="_blank" rel="noopener noreferrer" href="https://www.facebook.com/MorganMarshallismyname" className="tooltipped fa fa-facebook z-depth-4 "><span style={{display: 'none'}}>Facebook</span></a>
                <a data-position="top" data-tooltip="LinkedIn" target="_blank" rel="noopener noreferrer" href="https://www.linkedin.com/in/morgan-marshall-837882a9/" className="tooltipped fa fa-linkedin z-depth-3"><span style={{display: 'none'}}>LinkedIn</span></a>
                <a data-position="top" data-tooltip="GitHub" target="_blank" rel="noopener noreferrer" href="https://github.com/MorganMarshall" className="tooltipped fa fa-github z-depth-3"><span style={{display: 'none'}}>GitHub</span></a>
               
               
              </div>
            <div style={{paddingBottom: '2px'}} className="center">
            <div className="ftrtext" >imorganmarshall@gmail.com</div>
                <div className="ftrtext">Created with <a target="_blank" rel="noopener noreferrer" href="https://github.com/facebook/react">React.js</a> and <a target="_blank" rel="noopener noreferrer" href="https://github.com/ReactTraining/react-router">React Router</a></div>
                </div>
             
             
            </footer>
            </div>
          
        )
    }
}

export default Footer


